.header {
  background-color: #393E36;
  height: 80px;
  width: 100%;
  padding: 1.5em 4em;
}
.header .content-header {
  display: flex;
  justify-content: flex-start;
  align-items: center;
  width: 100%;
  height: 100%;
  gap: 30em;
}
@media screen and (max-width: 1000px) {
  .header .content-header {
    gap: 20em;
  }
}

.menu-header .menus {
  display: flex;
  justify-content: center;
  gap: 2em;
  align-items: center;
}
@media screen and (max-width: 800px) {
  .menu-header .menus {
    display: none;
  }
}
.menu-header .menus a {
  text-align: center;
  color: #EEEEEE;
}
.menu-header .navbar {
  position: relative;
  padding: 1rem;
  display: none;
}
.menu-header .navbar .menu-toggle {
  cursor: pointer;
}
.menu-header .navbar .menu-toggle .bar {
  display: block;
  width: 35px;
  height: 5px;
  background-color: #EEEEEE;
  margin: 5px auto;
  transition: 0.3s;
  border-radius: 10px;
}
@media (max-width: 800px) {
  .menu-header .navbar {
    display: block;
  }
}

.header-menu-mobile {
  background-color: #222831;
}
.header-menu-mobile .menus-mobile {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  padding: 1em 0;
  width: 100%;
}
.header-menu-mobile .menus-mobile a {
  text-align: center;
  color: #EEEEEE;
  width: 100%;
  border-bottom: 1px solid #EEEEEE;
  padding: 1.1em 0;
}
.header-menu-mobile .menus-mobile a:first-child {
  padding-top: 0;
}
.header-menu-mobile .menus-mobile a:last-child {
  border-bottom: none;
  padding: 0;
  padding-top: 1.1em;
}

main {
  min-height: 650px;
}

.container {
  max-width: 1280px;
  width: 100%;
  margin: 1rem auto;
}

.banner {
  width: 100%;
  height: 350px;
  background-image: url("../assets/images/bannerpilar.webp");
  background-repeat: no-repeat;
  background-size: cover;
  position: relative;
}

.banner-text {
  position: absolute;
  top: 60px;
  left: 60px;
}
.banner-text h2 {
  font-size: 50px;
  color: #EEEEEE;
  font-weight: bold;
}

.introduction h1 {
  font-size: 40px;
  text-align: center;
  padding: 0.7rem 0;
  font-family: "rubikRegular", "Courier New", Courier, monospace;
  background-color: rgba(246, 242, 242, 0.6);
  width: 800px;
  margin: 0 auto;
}

.content-introduction {
  display: flex;
  justify-content: center;
  gap: 4rem;
  align-items: center;
  padding: 2rem 0 0 0;
}

.introduction-img {
  width: 450px;
  height: 450px;
}
.introduction-img img {
  width: 100%;
  height: 100%;
  border-radius: 10px;
}

.introduction-text {
  width: 100%;
  height: 100%;
  align-self: self-start;
  max-width: 45%;
  font-size: 18px;
  line-height: 1.7rem;
  font-family: "rubikRegular", "Courier New", Courier, monospace;
}
.introduction-text button {
  padding: 0.8rem 4rem;
  margin-top: 5rem;
  background-color: #036752;
  border: none;
  cursor: pointer;
  font-weight: 700;
  font-family: "rubikSemiBold", "Courier New", Courier, monospace;
}
.introduction-text p {
  padding-top: 1rem;
}

/* second section */
.help {
  width: 100%;
  background-color: #3474a7;
}
.help .content-help {
  padding: 1rem 0;
  display: flex;
  justify-content: center;
  align-items: center;
  gap: 4rem;
}
.help .content-help .content-help-text {
  width: 100%;
  height: 100%;
  align-self: flex-start;
}
.help .content-help .content-help-text h2 {
  font-size: 30px;
  font-family: "rubikBold", "Courier New", Courier, monospace;
  background-color: rgba(246, 242, 242, 0.6);
  padding: 0.5rem;
  text-align: center;
}
.help .content-help .content-help-text .help-text-list {
  padding-top: 2rem;
}
.help .content-help .content-help-text .help-text-list ul {
  display: flex;
  justify-content: center;
  gap: 2rem;
}
.help .content-help .content-help-text .help-text-list ul div {
  display: flex;
  justify-content: center;
  gap: 2rem;
  flex-direction: column;
  align-self: flex-start;
}
.help .content-help .content-help-text .help-text-list ul div li {
  background-color: rgba(246, 242, 242, 0.6);
  width: auto;
  max-width: 300px;
  padding: 0.7rem 0.5rem;
  border-radius: 5px;
  font-family: "rubikSemiBold", "Courier New", Courier, monospace;
}

/*  third section */
.content-service h2 {
  font-size: 30px;
  text-align: center;
  padding: 0.7rem 0;
  font-family: "rubikRegular", "Courier New", Courier, monospace;
  background-color: rgba(246, 242, 242, 0.6);
  width: 800px;
  margin: 0 auto;
}
.content-service .flex-content-service {
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 2rem 0;
}
.content-service .card-service {
  width: 250px;
  height: 250px;
}

.footer {
  background-color: #393E36;
  width: 100%;
}

.content-footer {
  display: flex;
  justify-content: space-around;
  align-items: flex-start;
  padding: 1.5rem 4rem;
}

.contatos, .localizacao {
  width: 100%;
  height: 100%;
  justify-content: start;
  align-items: flex-start;
}
.contatos p, .localizacao p {
  font-size: 24px;
  color: #EEEEEE;
  margin-bottom: 1.5rem;
  font-weight: 700;
}
.contatos ul, .localizacao ul {
  display: flex;
  flex-direction: column;
  gap: 1.5rem;
  justify-content: start;
  align-items: flex-start;
  color: #EEEEEE;
  font-size: 14px;
}
.contatos ul li span, .localizacao ul li span {
  font-size: 20px;
  font-weight: bold;
}